import React, { useEffect } from "react"
import { Link } from "gatsby"
import ClipboardJS from "clipboard"
import { Wavy } from "../animations/wavy"

const Footer = () => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      document.getElementById("email").addEventListener("mouseover", () => {
        document.querySelector(".msg-box").style.display = "flex"
        document.querySelector(".msg").innerText =
            "Click to copy on your clipboard!"
      })
      document.getElementById("email").addEventListener("mouseleave", () => {
        document.querySelector(".msg-box").style.display = "none"
      })
      document.getElementById("email").addEventListener("click", () => {
        document.querySelector(".msg").innerText = "Copied!"
      })

      new ClipboardJS("#email")
    }
  }, [])

  return (
    <footer className="footer">
      <div className="msg-box">
        <p className="msg" />
      </div>
      <div className="social">
        <Link className="wavy" to="/">
          {Wavy("Twitter")}
        </Link>
        <Link className="wavy" to="/">
          {Wavy("LinkedIn")}
        </Link>
        <div
          id="email"
          className="wavy"
          data-clipboard-text="4rsh4vir4z4rf4r@gmail.com"
        >
          {Wavy("Email")}
        </div>
      </div>
    </footer>
  )
}

export default Footer
